import React, { useState } from "react";

const OrderForm = ({ deviceId, onSubmit }) => {
  const [partCode, setPartCode] = useState("");
  const [partName, setPartName] = useState("");

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log(`Zamwiono cz dla urzdzenia ${deviceId}:`, {
      partCode,
      partName,
    });
    //  doda logik wysyania zamwienia
    setPartCode("");
    setPartName("");
    onSubmit(); // funkcja przekazan z props po zoeniu zamwienia
  };

  return (
    <form onSubmit={handleSubmit} className="order-form">
      <h4>Zamw cz dla urzdzenia {deviceId}</h4>
      <input
        type="text"
        value={partCode}
        onChange={(e) => setPartCode(e.target.value)}
        placeholder="Kod czci"
      />
      <input
        type="text"
        value={partName}
        onChange={(e) => setPartName(e.target.value)}
        placeholder="Nazwa czci"
      />
      <button type="submit">Zamw Cz</button>
    </form>
  );
};

export default OrderForm;
