import React from "react";
import { useNavigate } from "react-router-dom";

const HomePage = () => {
  const navigate = useNavigate();

  const handleRoleSelect = (role) => {
    navigate(`/dashboard/${role}`);
  };

  return (
    <div className="role-selection">
      <h1>Wybr Roli</h1>
      <div>
        <button onClick={() => handleRoleSelect("technik")}>Technik</button>
        <button onClick={() => handleRoleSelect("koordynator")}>
          Koordynator
        </button>
        <button onClick={() => handleRoleSelect("kierownik")}>Kierownik</button>
      </div>
    </div>
  );
};

export default HomePage;
