import React from "react";


const OrderList = () => {
  const orders = [
    { id: 1, partCode: "123", partName: "Cz 1" },
    { id: 2, partCode: "456", partName: "Cz 2" },
  ];

  return (
    <div className="order-list">
      <h2>Lista Zamwie</h2>
      <ul className="list">
        {orders.map((order) => (
          <li key={order.id} className="list-item order-item-transform">
           Urzdzenie "..." {order.partCode} - {order.partName} 
          </li>
        ))}
      </ul>
    </div>
  );
};

export default OrderList;
